import { useEffect, useState } from "react"

const date = new Date()
const day = date.getDate().toString()
const month = date.getMonth() + 1
const hours = date.getHours().toString()
const mins = Number(date.getMinutes())


function DateHours() {
  const m = mins < 10 ? '0' + mins : mins
  let month_geo = '';

  switch (month) {
    case 1:
      month_geo = ''
      break;
    case 2:
      month_geo = ''
      break;
    case 3:
      month_geo = ''
      break;
    case 4:
      month_geo = ''
      break;
    case 5:
      month_geo = ''
      break;
    case 6:
      month_geo = ''
      break;
    case 7:
      month_geo = ''
      break;
    case 8:
      month_geo = ''
      break;
    case 9:
      month_geo = ''
      break;
    case 10:
      month_geo = ''
      break;
    case 11:
      month_geo = ''
      break;
    case 12:
      month_geo = ''
      break;

    default: <div></div>
      break;
  }
  const fullMonth = day + ' ' + month_geo;
  const fullHour = hours + ':' + m;

  return (
    <div className="flex items-center">
      <div className="w-[40px] h-[40px] flex items-center justify-center bg-sidebar-white rounded-full mr-3"><img src="/images/calendar.svg" alt="" /></div>
      <div className="flex flex-col ">
        <div className="font-helvetica text-xs text-white opacity-70">
          {fullMonth}
        </div>
        <div className="font-myriad text-sm text-white">
          {fullHour}
        </div>
      </div>
    </div>
  )
}

export default DateHours